import React from 'react';
import styles from './Menu.module.css';
import Header from '../Header/Header';
import {NavLink} from 'react-router-dom';
import Vaga from '../Vaga/Vaga';
import History from '../History/History';
import Add from '../Add/Add';
import {Switch, Route} from 'react-router-dom';
const Menu = ({growth,weight,match,wantWeight,date,resultSub,male,info,gender,res,newWeight,inputChange,arrWeight,arrData}) => {
    // console.log(arrWeight);
    return (
        <div className="menu">
            <Header match={match}/>
            
           <ul className={styles.ul}>
              <NavLink className={styles.but_vaga} to={`${match.path}/`}>Vaga</NavLink>
              <NavLink className={styles.but_history} to={`${match.path}/history`}>History</NavLink>
           </ul>
           <Switch>
               {/* <Route path={`${match.path}/add`}render={(props)=>
               <Add
               ewWeight={newWeight} 
               inputChange={inputChange}
               {...props}
               />
            }/> */}
            <Route exact path={`${match.path}/`} render={() =>
            <Vaga 
            
            arrWeight={arrWeight}
            newWeight={newWeight}
            growth={growth} 
            res={res}
            weight={weight}
            gender={gender}
            wantWeight={wantWeight}
            date={date}
            info={info}
            resultSub={resultSub}
            male={male}/>}/>
            <Route path={`${match.path}/history`} render={ (props) => 
            <History 
            wantWeight={wantWeight}
            info={info}
            weight={weight}
            arrWeight={arrWeight}
            arrData={arrData}
            

             {...props}/>}/>
            
            </Switch>
           {/* <Vaga growth={growth} weight={weight} wantWeight={wantWeight}/> */}
           </div>
    );
};

export default Menu;