import React from 'react';
import './Add.css';
import {NavLink} from 'react-router-dom';
const Add = ({newWeight,inputChange,addWeight,newAddWeight,weight}) => {
    return (
        <div>
             <input type="text" name="newWeight" className="add" value={newWeight} onChange={inputChange}/>
             {/* <input type="text" name="weight" className="weight" value='' onChange={inputChange}/> */}
             <button onClick={addWeight} ><NavLink to='/save'>Добавить</NavLink></button>
             <button><NavLink to='/save'>Отмена</NavLink></button>
        </div>
    );
};

export default Add;

