import React from 'react';
import './Vaga.css';
import ReactSpeedometer from "react-d3-speedometer"
import moment from 'moment';
const Vaga = ({weight, growth,wantWeight,date,resultSub,male,info,gender,res,arrWeight,newWeight}) => {
    console.log(arrWeight);
    // const weightV= arrWeight.map(el=>el.weight);
    
    // console.log(weightV);
    return (
     
        <div className="root_vaga">

            <div className='vaga_now'>
            <p>{JSON.parse(localStorage.getItem('gender')).male ? 'Пол: мужской' :'Пол: женский'}</p>
              <p className="vaga_title_now_weight">Текущий вес:</p>
              <p className="vaga_now_weight">{arrWeight[arrWeight.length-1].weight} <span>kg</span></p>
             {/* <p className="vaga_now_weight">{JSON.parse(localStorage.getItem('arrWeight')).weight} <span>kg</span></p> */}
              <p>{moment().format('L')}</p>
            </div>
            
            <div className="vaga_desired">
                <p className='vaga_title_desired_weight'>Желаемый вес:</p>
                <p className='vaga_desired_weight'>{JSON.parse(localStorage.getItem('info')).wantWeight}<span>kg</span></p> 
                <p>{`Разница ${resultSub} кг`}</p>   
                
            </div> 
            
            <div>
                <button>Info</button>
            </div>
            <div className="speedometer">
                   <ReactSpeedometer
                        maxValue={40}
                        value={+(arrWeight[arrWeight.length-1].weight/((JSON.parse(localStorage.getItem('info')).growth/100)*(JSON.parse(localStorage.getItem('info')).growth/100))).toFixed(2)}
                        needleColor="red"
                        startColor="green"
                        segments={10}
                        endColor="red"
                    />
            </div>

        </div>
    );
};

export default Vaga;

// валюе вытащить создать константу и приравнять эту формулу и прокидывать пропсом
// разница в вес проверить на длину массива что-бы было минимум 2 элемента
// желаемый вес проверить что-бы стейт был не пустой (если пустой то пустой массив или пустая строка)
