import React, { Component } from 'react';
import './App.css';
import Add from '../Add/Add';
import StartPage from '../StartPage/StartPage';
import Menu from '../Menu/Menu';
import {Switch, Route} from 'react-router-dom';
import Vaga from '../Vaga/Vaga';

import moment from 'moment';
import  History from '../History/History.js'
class App extends Component {
  state={
    // male:  JSON.parse(localStorage.getItem('male')) ||'',
    // female:  JSON.parse(localStorage.getItem('femail')) || '',
    growth: JSON.parse(localStorage.getItem('growth')) || '',
    weight: JSON.parse(localStorage.getItem('weight')) || '',
    wantWeight: JSON.parse(localStorage.getItem('wantWeight')) ||'',
    date: '',
    newWeight: '',
    resultSub: JSON.parse(localStorage.getItem('resultSub'))|| 0,
    info: {
      weight: '',
      growth: '',
      wantWeight: '',
    } ,
    gender: {
      male:  '',
      femail:  '',
    },
    arrWeight: JSON.parse(localStorage.getItem('arrWeight')) || [],
    arrData: JSON.parse(localStorage.getItem('arrData')) || [],
  }
  
  
 
  // componentDidMount(){
  //   this.time();
   
  //     }
 
 addWeight=async()=>{
  let newData=moment().format('dddd Do')
  const obj ={
    id: newData,
    weight: this.state.newWeight,
  }
   await this.setState(prev=>({
     arrWeight: [...prev.arrWeight,obj],
    //  arrData: [...prev.arrData,newData]

   }))
   localStorage.setItem('arrWeight', JSON.stringify(this.state.arrWeight));
  //  localStorage.setItem('arrData', JSON.stringify(this.state.arrData));
 }

  checkMale=async()=>{
   await this.setState({
      gender:{male:true,
            femail:false}
      
      })
      localStorage.setItem('gender', JSON.stringify(this.state.gender));
  }
  checkFemale=async()=>{
    await this.setState({
      gender:{male: false,
              femail: true},
      })
      localStorage.setItem('gender', JSON.stringify(this.state.gender));
  }
  
  inputChange=(e)=>{
      // console.log(e);
    let value=e.target.value;
    let name=e.target.name;
    this.setState({
        [name]: value,
        
    })
            
  }
  infowraight=async()=>{
    let newData=moment().format('dddd Do')
    const obj2 ={
      id: newData,
      weight: this.state.weight,
    }
    await this.setState(prev=>({
      info:{
        // weight: this.state.weight,
        wantWeight: this.state.wantWeight,
        growth: this.state.growth,
        },
        arrWeight: [...prev.arrWeight,obj2],
    }))
    localStorage.setItem('arrWeight', JSON.stringify(this.state.arrWeight))
    localStorage.setItem('info',JSON.stringify(this.state.info))

   }
  // time=()=>{
  //   setInterval(()=>{
  //     let getTime=moment().format('L');
  //     this.setState({
  //       date: getTime,
  //     })
  //   }, 0)
  // }
  sub= async()=>{
    await this.setState({
      resultSub: this.state.weight - this.state.wantWeight,
    })
    localStorage.setItem('resultSub', JSON.stringify(this.state.resultSub))
  }

  render() {
    const{gender,growth,weight,wantWeight,date,resultSub,male,female,info,newWeight,arrWeight,arrData}=this.state;
    const res = JSON.parse(localStorage.getItem('info'));
    // localStorage.clear()
    return (
      <div>
        
        {/* <Menu/> */}
        
        <Switch>
            <Route exact path="/" render={() =>
            <StartPage 
            gender={gender} 
            growth={growth} 
            weight={weight}
            sub={this.sub}
            male={male}
            female={female}
            addWeight={this.addWeight}
            infowraight={this.infowraight}
            checkMale={this.checkMale}
            checkFemale={this.checkFemale}
            inputChange={this.inputChange}/>}/>
            <Route path={'/add'}render={(props)=>
               <Add
               addWeight={this.addWeight}
               weight={weight}
               ewWeight={newWeight} 
               inputChange={this.inputChange}
               {...props}
               />
            }/>
            <Route path="/save" render={ (props) => 
            <Menu  
            gender={gender}
            growth={growth} 
            arrData={arrData}
            arrWeight={arrWeight}
            res={res}
            info={info}
            weight={weight} 
            wantWeight={wantWeight}
            date={date}
            male={male} 
            gender={gender}
            inputChange={this.inputChange}
            resultSub={resultSub}
            newWeight={newWeight}
            // arrWeight={arrWeight}
            {...props}/>}/>
            {/* <Route path="/history" render={ (props) => 
            <History 
            weight={weight} {...props}/>}/> */}
           
        </Switch>
        {/* <Vaga growth={growth} weight={weight}/> */}
        {/* <History  weight={weight} /> */}
      </div>
    );
  }
}

export default App;



