import React from 'react';
import './History.css';
import {Line} from 'react-chartjs-2';
const History = ({arrWeight,arrData, info, wantWeight}) => {
    // const weightSub= arrWeight[arrWeight.lenght-1].weight-arrWeight[arrWeight.lenght-2].weight;
    // console.log(weightSub);
    const data={
        labels: arrWeight.map(el=>el.id),
        // labels: JSON.parse(localStorage.getItem('arrWeight.id')),
        datasets:[{
            label: 'weight',
            data: arrWeight.map(el=>el.weight)
            // data: JSON.parse(localStorage.getItem('arrWeight.weight'))
        }]
        
    }
    // let want=
    // console.log(want);
    return (
        <div>
            <Line 
            data={data}
            width={100}
            height={100}
            />
            <div className='tablHistory'>
            <ul className='tablHistoryUl'>{arrWeight.map(el=><li key={el.weight}><span>{el.weight}</span><span>{el.id}</span><span>{}</span></li>)}</ul>
            {/* <ul className='tablHistoryUl'>{arrData.map(el=><li>{el} </li>)}</ul> */}
            {/* <ul>{arrWeight.map(el=><li>{el}-{info.wantWeight} </li>)}</ul> */}
            </div>
        </div>
    );
};

export default History;